"use client";

import type { ReactNode } from "react";
import { createContext, useContext, useState } from "react";

import { WaitlistModal } from "./waitlist-modal";

interface WaitlistModalContextType {
  openModal: () => void;
  closeModal: () => void;
  isOpen: boolean;
}

const WaitlistModalContext = createContext<
  WaitlistModalContextType | undefined
>(undefined);

export function WaitlistModalProvider({ children }: { children: ReactNode }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <WaitlistModalContext.Provider
      value={{
        openModal: () => setIsOpen(true),
        closeModal: () => setIsOpen(false),
        isOpen,
      }}
    >
      {children}
      <WaitlistModal open={isOpen} onOpenChange={setIsOpen} />
    </WaitlistModalContext.Provider>
  );
}

export function useWaitlistModal() {
  const context = useContext(WaitlistModalContext);
  if (context === undefined) {
    throw new Error(
      "useWaitlistModal must be used within a WaitlistModalProvider",
    );
  }
  return context;
}
