"use client";

import { useState } from "react";
import Image from "next/image";
import { useForm } from "@tanstack/react-form";

import { Button } from "@trading-platform/ui/button";
import { CircularProgress } from "@trading-platform/ui/circular-progress";
import { Dialog, DialogClose, DialogContent } from "@trading-platform/ui/dialog";
import { Input } from "@trading-platform/ui/input";
import { Label } from "@trading-platform/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@trading-platform/ui/select";
import { Textarea } from "@trading-platform/ui/textarea";

import { ASSETS } from "./constants";
import { useWaitlistStats } from "./use-waitlist-stats";

interface WaitlistModalProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export function WaitlistModal({ open, onOpenChange }: WaitlistModalProps) {
  const { stats } = useWaitlistStats();
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const form = useForm({
    defaultValues: {
      restaurant_name: "",
      number_of_locations: "",
      full_name: "",
      email: "",
      phone: "",
      biggest_challenge: "",
    },
    onSubmit: async ({ value }) => {
      setLoading(true);
      setError(null);

      try {
        const response = await fetch("/api/waitlist", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email: value.email,
            full_name: value.full_name,
            phone: value.phone || null,
            restaurant_name: value.restaurant_name,
            number_of_locations: value.number_of_locations
              ? parseInt(value.number_of_locations)
              : null,
          }),
        });

        const data = (await response.json()) as {
          success?: boolean;
          error?: string;
        };

        if (!response.ok) {
          throw new Error(data.error ?? "Failed to submit application");
        }

        setSuccess(true);
      } catch (err) {
        setError(err instanceof Error ? err.message : "Something went wrong");
      } finally {
        setLoading(false);
      }
    },
  });

  const handleClose = () => {
    if (!loading) {
      onOpenChange(false);
      // Reset form after a delay to allow animation
      setTimeout(() => {
        setSuccess(false);
        setError(null);
        form.reset();
      }, 200);
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent
        className="max-w-[690px] overflow-hidden rounded-[10px] border-white/20 bg-[#0c0c0c] p-0"
        showCloseButton={false}
      >
        <div className="flex h-[769px]">
          {/* Left Sidebar */}
          <div className="flex w-[256px] flex-col gap-16 bg-gradient-to-b from-white/10 to-white/5 p-8">
            <div className="flex flex-col gap-8">
              <div className="flex flex-col gap-4">
                <div className="flex items-center gap-2">
                  <div className="flex h-8 w-8 items-center justify-center rounded-[8px] bg-white">
                    <Image
                      src={ASSETS.icon}
                      alt=""
                      width={24}
                      height={24}
                      unoptimized
                    />
                  </div>
                </div>
                <h2 className="text-[30px] leading-[36px] font-normal tracking-[0.3955px] text-zinc-300">
                  Limited Early Access
                </h2>
                <p className="text-base leading-[26px] tracking-[-0.3125px] text-[#9f9fa9]">
                  Join the first 50 restaurants to shape the future of
                  restaurant operations.
                </p>
              </div>

              {/* Value Points */}
              <div className="flex flex-col gap-3">
                {[
                  { title: "90 Days Free", desc: "No cost for first 3 months" },
                  {
                    title: "Lifetime Discount",
                    desc: "Lock in $120/month rate",
                  },
                  {
                    title: "Shape the Product",
                    desc: "Your feedback drives features",
                  },
                ].map((item, idx) => (
                  <div key={idx} className="flex items-start gap-3">
                    <div className="mt-0.5 flex h-6 w-6 shrink-0 items-center justify-center rounded-full border border-white/20 bg-white/10">
                      <Image
                        src={ASSETS.checkIcon}
                        alt=""
                        width={14}
                        height={14}
                        className="h-3.5 w-3.5"
                        unoptimized
                      />
                    </div>
                    <div className="flex flex-col">
                      <p className="text-sm leading-5 font-medium tracking-[-0.1504px] text-white">
                        {item.title}
                      </p>
                      <p className="text-xs leading-4 tracking-[-0.1504px] text-[#71717b]">
                        {item.desc}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Spots Remaining */}
            <div className="flex flex-col gap-4">
              <div className="flex items-center justify-between">
                <Label className="text-sm font-medium tracking-[-0.1504px] text-[#d9d9d9]">
                  Spots Remaining
                </Label>
                <div className="relative h-8 w-8" suppressHydrationWarning>
                  <CircularProgress
                    value={stats.registeredCount}
                    max={stats.maxSpots}
                    size={32}
                  />
                </div>
              </div>
              <div className="rounded-[10px] border border-white/10 bg-gradient-to-b from-white/10 to-white/5 p-4">
                <div className="flex items-start gap-2.5">
                  <div className="mt-0.5 h-3.5 w-3.5 shrink-0">
                    <Image
                      src={ASSETS.icon4}
                      alt=""
                      width={14}
                      height={14}
                      unoptimized
                    />
                  </div>
                  <p className="flex-1 text-xs leading-4 tracking-[-0.1504px] text-[#9f9fa9]">
                    Early access closes when all 50 spots are filled.
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Right Form Section */}
          <div className="flex flex-1 flex-col bg-gradient-to-b from-white/10 to-white/5 p-8">
            {success ? (
              <div className="flex flex-1 flex-col items-center justify-center gap-6 text-center">
                <div className="mb-2 text-6xl">ðŸš€</div>
                <h3 className="text-2xl font-medium text-white">
                  Application Submitted!
                </h3>
                <p className="max-w-md text-base text-[#9f9fa9]">
                  We&apos;ll review your application within 48 hours and reach
                  out to schedule your personalized demo.
                </p>
                <div className="mt-4 space-y-2 text-sm text-[#9f9fa9]">
                  <p className="font-semibold text-white">What&apos;s next:</p>
                  <ul className="space-y-1 text-left">
                    <li>âœ“ 90 days completely free</li>
                    <li>âœ“ White-glove onboarding</li>
                    <li>âœ“ Direct line to our founding team</li>
                  </ul>
                </div>
                <Button
                  onClick={handleClose}
                  className="mt-4 bg-gradient-to-r from-white to-[#f4f4f5] text-black hover:from-white/90 hover:to-[#f4f4f5]/90"
                >
                  Close
                </Button>
              </div>
            ) : (
              <>
                <div className="mb-6">
                  <h3 className="mb-1 text-xl leading-7 font-normal tracking-[-0.4492px] text-white">
                    Your Information
                  </h3>
                  <p className="text-sm leading-5 tracking-[-0.1504px] text-[#9f9fa9]">
                    Complete the form to secure your spot
                  </p>
                </div>

                <form
                  onSubmit={(e: React.FormEvent) => {
                    e.preventDefault();
                    e.stopPropagation();
                    void form.handleSubmit();
                  }}
                  className="flex flex-1 flex-col gap-9"
                >
                  <div className="flex-1 space-y-9 overflow-y-auto pr-2">
                    {/* Restaurant Name */}
                    <div className="space-y-2">
                      <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                        Restaurant Name *
                      </Label>
                      <form.Field
                        name="restaurant_name"
                        validators={{
                          onChange: ({ value }) => {
                            if (!value) return "Restaurant name is required";
                            return undefined;
                          },
                        }}
                      >
                        {(field) => (
                          <>
                            <Input
                              placeholder="Enter restaurant name"
                              value={field.state.value}
                              onChange={(
                                e: React.ChangeEvent<HTMLInputElement>,
                              ) => field.handleChange(e.target.value)}
                              onBlur={field.handleBlur}
                              disabled={loading}
                              className="h-11 border-white/20 bg-[rgba(8,8,9,0.25)] text-white placeholder:text-[#71717b]"
                              required
                            />
                            {field.state.meta.errors.length > 0 && (
                              <p className="text-xs text-red-500">
                                {field.state.meta.errors[0]}
                              </p>
                            )}
                          </>
                        )}
                      </form.Field>
                    </div>

                    {/* Number of Locations */}
                    <div className="space-y-2">
                      <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                        Number of Locations *
                      </Label>
                      <form.Field
                        name="number_of_locations"
                        validators={{
                          onChange: ({ value }) => {
                            if (!value)
                              return "Number of locations is required";
                            return undefined;
                          },
                        }}
                      >
                        {(field) => (
                          <>
                            <Select
                              value={field.state.value || ""}
                              onValueChange={(value: string) =>
                                field.handleChange(value)
                              }
                              disabled={loading}
                            >
                              <SelectTrigger className="h-9 w-full border-white/20 bg-[rgba(8,8,9,0.25)] text-white">
                                <SelectValue placeholder="Select..." />
                              </SelectTrigger>
                              <SelectContent className="border-white/20 bg-[#0c0c0c]">
                                {[1, 2, 3, 4, 5, "6+"].map((num) => (
                                  <SelectItem
                                    key={num}
                                    value={String(num)}
                                    className="text-white focus:bg-white/10"
                                  >
                                    {num}
                                  </SelectItem>
                                ))}
                              </SelectContent>
                            </Select>
                            {field.state.meta.errors.length > 0 && (
                              <p className="text-xs text-red-500">
                                {field.state.meta.errors[0]}
                              </p>
                            )}
                          </>
                        )}
                      </form.Field>
                    </div>

                    {/* Your Name */}
                    <div className="space-y-2">
                      <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                        Your Name *
                      </Label>
                      <form.Field
                        name="full_name"
                        validators={{
                          onChange: ({ value }) => {
                            if (!value) return "Your name is required";
                            return undefined;
                          },
                        }}
                      >
                        {(field) => (
                          <>
                            <Input
                              placeholder="Enter your name"
                              value={field.state.value}
                              onChange={(
                                e: React.ChangeEvent<HTMLInputElement>,
                              ) => field.handleChange(e.target.value)}
                              onBlur={field.handleBlur}
                              disabled={loading}
                              className="h-11 border-white/20 bg-[rgba(8,8,9,0.25)] text-white placeholder:text-[#71717b]"
                              required
                            />
                            {field.state.meta.errors.length > 0 && (
                              <p className="text-xs text-red-500">
                                {field.state.meta.errors[0]}
                              </p>
                            )}
                          </>
                        )}
                      </form.Field>
                    </div>

                    {/* Email and Phone */}
                    <div className="grid grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                          Email *
                        </Label>
                        <form.Field
                          name="email"
                          validators={{
                            onChange: ({ value }) => {
                              if (!value) return "Email is required";
                              if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value))
                                return "Invalid email address";
                              return undefined;
                            },
                          }}
                        >
                          {(field) => (
                            <>
                              <Input
                                type="email"
                                placeholder="joe@restaurant.com"
                                value={field.state.value}
                                onChange={(e) =>
                                  field.handleChange(e.target.value)
                                }
                                onBlur={field.handleBlur}
                                disabled={loading}
                                className="h-11 border-white/20 bg-[rgba(8,8,9,0.25)] text-white placeholder:text-[#71717b]"
                                required
                              />
                              {field.state.meta.errors.length > 0 && (
                                <p className="text-xs text-red-500">
                                  {field.state.meta.errors[0]}
                                </p>
                              )}
                            </>
                          )}
                        </form.Field>
                      </div>

                      <div className="space-y-2">
                        <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                          Phone
                        </Label>
                        <form.Field name="phone">
                          {(field) => (
                            <Input
                              type="tel"
                              placeholder="+1 (555) 123-4567"
                              value={field.state.value}
                              onChange={(
                                e: React.ChangeEvent<HTMLInputElement>,
                              ) => field.handleChange(e.target.value)}
                              onBlur={field.handleBlur}
                              disabled={loading}
                              className="h-11 border-white/20 bg-[rgba(8,8,9,0.25)] text-white placeholder:text-[#71717b]"
                            />
                          )}
                        </form.Field>
                      </div>
                    </div>

                    {/* Biggest Challenge */}
                    <div className="space-y-2">
                      <Label className="text-sm font-medium tracking-[-0.1504px] text-zinc-300">
                        Biggest Challenge (Optional)
                      </Label>
                      <form.Field name="biggest_challenge">
                        {(field) => (
                          <Textarea
                            placeholder="Tell us about your main operational challenge..."
                            value={field.state.value}
                            onChange={(
                              e: React.ChangeEvent<HTMLTextAreaElement>,
                            ) => field.handleChange(e.target.value)}
                            onBlur={field.handleBlur}
                            disabled={loading}
                            rows={4}
                            className="resize-none border-white/20 bg-[rgba(8,8,9,0.25)] text-white placeholder:text-[#71717b]"
                          />
                        )}
                      </form.Field>
                    </div>
                  </div>

                  {/* Error Message */}
                  {error && (
                    <div className="mt-4 text-sm text-red-500">{error}</div>
                  )}

                  {/* Submit Button */}
                  <div className="mt-auto border-t border-white/10 pt-4">
                    <Button
                      type="submit"
                      disabled={loading}
                      className="h-11 w-full bg-gradient-to-r from-white to-[#f4f4f5] text-sm font-medium tracking-[-0.1504px] text-black hover:from-white/90 hover:to-[#f4f4f5]/90"
                    >
                      {loading ? "Submitting..." : "Submit Application"}
                    </Button>
                  </div>
                </form>
              </>
            )}

            {/* Close Button */}
            <DialogClose asChild>
              <button
                className="absolute top-4 right-4 opacity-70 transition-opacity hover:opacity-100"
                onClick={handleClose}
                disabled={loading}
              >
                <Image
                  src={ASSETS.closeIcon}
                  alt="Close"
                  width={24}
                  height={24}
                  className="h-6 w-6"
                  unoptimized
                />
                <span className="sr-only">Close</span>
              </button>
            </DialogClose>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
